import { Component } from 'react';
import axios from 'axios';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);

    this.state = {
      input: 'Harry Potter',
      result: [],
      hasErrors: false,
      loading: false
    }
  }

  componentDidMount(){
    const promise = axios.get("https://www.googleapis.com/books/v1/volumes?q="+this.state.input);
    promise
    .then((data) => {
      this.setState({result: data.data.items});
      console.log('inside then promise');
      this.setState({loading: true});
    })
    .catch(() => {
      this.setState({hasErrors: true});
      console.log('hasErrors inside catch', this.state.hasErrors);
    })
    // this.setState({input:''});
  }

  handleChange = e => {
    console.log('handleChange fired')
    this.setState({input: e.target.value});
  };

  handleSubmit = e => {
    console.log('handleSubmit fired')
    e.preventDefault();
    
    const promise = axios.get("https://www.googleapis.com/books/v1/volumes?q="+this.state.input);
    promise
      .then((data) => {
        this.setState({result: data.data.items});
        console.log('inside then promise');
        this.setState({loading: true});
      })
      .catch(() => {
        this.setState({hasErrors: true});
        console.log('hasErrors inside catch', this.state.hasErrors);
      })
      // this.setState({input:''});
  };


  render(){

    if(this.state.hasErrors){
      return(
        <div className="App">
          <h1>Please try again later.</h1>
        </div>
      )
    }
    else if(this.state.result){
      return (
        <div className="App">
            <h1>Book Search</h1>
            <form id="book-form" onSubmit={this.handleSubmit}>
                <input 
                  type="text" 
                  value={this.state.input}
                  onChange={this.handleChange}
                  placeholder="Search for a book" 
                  autoComplete="off" 
                />
                <button type="submit">Search</button>
            </form>
            <ul>
              {this.state.loading ? this.state.result.map(input => (
                <li key={input.volumeInfo.previewLink}>
                  <a href={input.volumeInfo.previewLink}>
                    <img className="image"
                      src={input.volumeInfo.imageLinks ? input.volumeInfo.imageLinks.thumbnail : null} 
                      alt={input.title}
                    />
                    <h2 style={{color: "black"}}>{input.volumeInfo.title}</h2>
                  </a>
                  <p><b>Categorie: </b>{input.volumeInfo.categories ? input.volumeInfo.categories : 'Not available'}</p>
                  <p className="description"><b>Description: </b> {input.volumeInfo.description ? input.volumeInfo.description : 'Not available'}</p>
                </li>
              )) : 'Loading...'}
            </ul>
        </div>
      );
  
    }else{
      return(
          <div className="App">
            <h1>Book Search</h1>
            <p>The book "{this.state.input}" was not found.</p>
        </div>
      )
    }
  }
}

export default App;